.filename-overlay {
    opacity: 0;
    position: "absolute";
    bottom: 0;
    left: 0;
    right: 0;
    /*backgroundColor: "rgba(0; 0; 0; 0.5)";*/
    color: "white";
    padding: "5px";
    fontSize: "14px";
    textAlign: "center";
    opacity: 0;
    transition: "opacity 0.3s ease";
    pointer-events: none; /* Ensures the overlay doesn't interfere with mouse events */
  }
  
  .filename-overlay :hover  {
    opacity: 1;
    pointer-events: auto; /* Enable mouse events when visible */
  }
  